<template>
  <div id="app">
  <router-view></router-view>
  </div>
</template>

<script>
export default {
  name: 'app',
  mounted() {
    const result = JSON.parse(localStorage.getItem('taskdata'))
    if(result) {
      this.$store.commit('setLocalstorageData', JSON.parse(localStorage.getItem('taskdata')))
    }
  },
}
</script>