import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

// const store = new Vuex.Store


const store = new Vuex.Store({
  state: {//グローバル変数のようなデーター
    tasks:[
    ],
    finishId: [
    ],
    taskId: [
    ],
    showContent: [
    ],
    nextTaskId: 1,
  },
  mutations: {//算出プロパティ
    addTask (state, { name }) {
      state.tasks.push ({
        id: state.nextTaskId,
        name,
        done:false,
        done_date: ''
      })
      state.nextTaskId++
    },
    // openModal (state,{ id }) {
    //   state.showContent.push
    //   this.showContent = true
    //   this.taskId = id;
    // },
    delTask(state, id) {//openModalに渡したidを削除する
      let date = new Date();
      let test = date.toLocaleDateString();
      // state.tasks = state.tasks.filter(task => task.id !== id)
      const result = state.tasks.findIndex(task => task.id === id)
      state.tasks[result].done = true
      state.tasks[result].done_date = test //日付
    },
    toggleTaskStatus (state, { id }) {
      const filtered = state.tasks.filter(task => {
        return task.id === id
      })
      filtered.forEach(task => {
        task.done =!task.done
      })
    },
    setLocalstorageData(state, data) {
      state.tasks = data
    }
  },
  // actions: {//メソッドのような存在(mutationでしかstateを変更できないようにルールする)
  //   save ({ state }) {
  //     const data = {
  //       tasks: state.tasks,
  //       finishId: state.finishId,
  //       taskId: state.taskId,
  //       showContent: state.showContent,
  //       nextTaskId: state.nextTaskId
  //     }
  //     localStorage.setItem('task-app-data', JSON.stringify(data))
  //   }
  // },
  // restore ({ commit }) {
  //   const data =localStorage.getItem('task-app-data')
  //   if (data) {
  //     commit('restore',JSON.parse(data))
  //   }
  // }
  // modules: {//非同期処理はactionに書く
  // }
})

export default store